@font-face {
    font-family: 'icomoon';
    src:  url('../fonts/icomoon.eot?7svvql');
    src:  url('../fonts/icomoon.eot?7svvql#iefix') format('embedded-opentype'),
      url('../fonts/icomoon.ttf?7svvql') format('truetype'),
      url('../fonts/icomoon.woff?7svvql') format('woff'),
      url('../fonts/icomoon.svg?7svvql#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
  }
  
  [class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
  
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  .icon-codepen:before {
    content: "\e900";
